.tours {
  &::after {
    position: absolute;
    content: "";
    top: 782px;
    right: 0;
    width: 602px;
    height: 1364px;
    background-image: image-set("../../img/clouds/clouds-1.png" type("image/png") 1x,
        "../../img/clouds/clouds-1@2x.png" type("image/png") 2x,
        "../../img/clouds/clouds-1.webp" type("image/webp") 1x,
        "../../img/clouds/clouds-1@2x.webp" type("image/webp") 2x);
    background-repeat: no-repeat;
    background-position: top right;

    @include vp-1199 {
      top: 929px;
      background-position: top right -307px;
    }

    @include vp-767 {
      width: 346px;
      height: 595px;
      top: 705px;
      // left: 144px;
      background-image: image-set("../../img/clouds/clouds-1-mobile.png" type("image/png") 1x,
          "../../img/clouds/clouds-1-mobile@2x.png" type("image/png") 2x,
          "../../img/clouds/clouds-1-mobile.webp" type("image/webp") 1x,
          "../../img/clouds/clouds-1-mobile@2x.webp" type("image/webp") 2x);
      background-position: top right -170px;
    }
  }

  &::before {
    position: absolute;
    content: "";
    top: 1302px;
    left: 0;
    width: 837px;
    height: 279px;
    background-image: image-set("../../img/clouds/clouds-2.png" type("image/png") 1x,
        "../../img/clouds/clouds-2@2x.png" type("image/png") 2x,
        "../../img/clouds/clouds-2.webp" type("image/webp") 1x,
        "../../img/clouds/clouds-2@2x.webp" type("image/webp") 2x);
    background-repeat: no-repeat;
    background-position: top left;

    @include vp-1199 {
      display: none;
    }
  }
}

.tours__wrapper {
  position: relative;
  z-index: 3;
  max-width: 1440px;
  margin: 0 auto;
  padding: 123px 120px 102px;

  @include vp-1199 {
    padding: 83px 45px 63px;
  }

  @include vp-767 {
    padding: 60px 15px 44px;
  }
}

.tours__swiper {
  position: relative;
  max-width: 1200px;
  height: 501px;
  color: $color-default-white;
  margin-bottom: 52px;

  @include vp-1199 {
    height: 485px;
    padding: 0 6px;
  }

  @include vp-767 {
    padding: 0;
    margin-bottom: 34px;
  }

  h3 {

    position: relative;
    z-index: 2;

    @include heading-h3;
    color: $color-primary-elephant;
    padding: 0;
    margin: 0;
    margin-right: auto;

    @include vp-1199 {
      margin-left: -6px;
      margin-top: 5px;
    }

    @include vp-767 {
      margin-top: 7px;
      margin-left: 0;
      width: min-content;
    }

    &::after {
      position: absolute;
      content: attr(data-heading);
      left: 0;
      bottom: 0;

      @include heading-h2;

      color: $color-primary-elephant;
      opacity: 0.03;
    }
  }
}

.tours__slide {
  position: relative;
  margin: 0 auto;
  align-self: flex-start;
  height: 412px;


  &::after {
    position: absolute;
    content: "";
    width: 100%;
    z-index: 2;
    top: 0;
    left: 0;
    height: 100%;
    background: $black-to-transparent;
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.tours__description {
  position: absolute;
  z-index: 3;
  bottom: 0;
  left: 0;
  padding: 25px 20px 25px 20px;
  overflow: auto;
  max-height: 412px;
  width: 100%;

  @include vp-767 {
    // bottom: 11px;

    padding: 25px 5px 25px 20px;
  }

  h4 {
    @include heading-h4;
    margin: 0;
    margin-right: 36px;
    margin-bottom: 38px;

    @include vp-1199 {
      margin-right: 50px;
    }

    @include vp-767 {
      margin-top: 11px;

      margin-right: 50px;
      margin-bottom: 31px;
    }
  }

  &--01 {
    h4 {
      margin-right: 70px;

      @include vp-1199 {
        margin-right: 65px;
      }
    }
  }
}

.tours__container {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  flex-wrap: wrap;
  margin-bottom: 23px;

  @include vp-767 {
    justify-content: flex-start;
    margin-bottom: 0;
  }
}

.tours__container p {
  position: relative;
  margin: 0;
  padding: 4px 0 8px 30px;
  display: flex;
  align-items: center;
  min-width: 76px;

  @include caption-medium;

  @include vp-1199 {
    min-width: 73px;
  }

  @include vp-767 {
    margin-bottom: 10px;
  }

  svg {
    position: absolute;
    top: 0;
    left: 0;
  }
}

.tours__container p:first-of-type {
  padding: 9px 25px 8px 39px;
  background-color: $color-navy-blue;
  min-width: 108px;
  text-align: center;

  @include vp-1199 {
    padding: 9px 25px 8px 30px;
    min-width: 92px;
  }

  @include vp-767 {
    order: 3;
    flex-basis: 50%;
    margin: 0;
    margin-bottom: 4px;
    min-width: min-content;
  }

  svg {
    position: absolute;
    top: 9px;
    left: 24px;

    @include vp-1199 {
      top: 9px;
      left: 16px;
    }
  }
}


.tours__container p:not(:last-of-type) {
  margin-right: 20px;

  @include vp-1199 {
    margin-right: 10px;
  }

  @include vp-767 {
    margin-right: 19px;
  }
}

.tours__details {
  margin: 0;
  padding: 0;
  display: block;
  column-count: 3;
  gap: 46px;

  @include vp-1199 {
    gap: 18px;
  }

  @include vp-767 {
    margin-top: 0;
    margin-bottom: -3px;
    gap: 2px;
  }
}

dt {
  margin: 0;
  margin-bottom: 7px;
  padding: 0;

  @include caption-small;
}

dt ~ dt ~ dt {
  padding-left: 3px;
}


dd {
  @include caption-big;
  margin: 0;
}

.tours__link {
  @include button-big;
  display: flex;
  justify-content: center;

  @include hover-focus {
    color: $color-resolution-blue;
    outline: none;

    svg {
      fill: none;
      stroke: currentColor;
    }
  }

  &:active {
    color: $color-navy-blue;

    svg {
      fill: none;
      stroke: currentColor;
    }
  }

  &:disabled {
    color: rgba(255, 255, 255, 0.1);

    svg {
      fill: none;
      stroke: currentColor;
    }
  }

  svg {
    margin-left: 12px;
    fill: none;
    stroke: $color-primary-elephant;
  }
}

.tours__button-box {

  width: 100%;
  margin-left: auto;
  display: flex;
  justify-content: flex-end;
  margin-bottom: 54px;

  @include vp-1199 {
    // min-width: 672px;
    margin-bottom: 39px;
  }

  @include vp-767 {
    margin-bottom: 27px;
  }
}

.tours__swiper-button {

  width: 37px;
  height: 35px;
  cursor: pointer;

  @include vp-1199 {
    margin-right: -6px;
  }

  @include vp-767 {
    margin-right: 0;
    margin-top: 9px;
  }

  &--prev {
    background-image: url("../../img/sprite/icon-arrow.svg");
    margin-right: 4px;

    @include hover-focus {
      background-image: url("../../img/sprite/icon-arrow-hf.svg");
      outline: none;
    }

    &:active {
      background-image: url("../../img/sprite/icon-arrow-active.svg");
    }

    &:disabled {
      background-image: url("../../img/sprite/icon-arrow-disabled.svg");
    }
  }

  &--next {
    rotate: 180deg;
    background-image: url("../../img/sprite/icon-arrow.svg");

    @include hover-focus {
      background-image: url("../../img/sprite/icon-arrow-hf.svg");
      outline: none;
    }

    &:active {
      background-image: url("../../img/sprite/icon-arrow-active.svg");
    }

    &:disabled {
      background-image: url("../../img/sprite/icon-arrow-disabled.svg");
    }
  }
}

.tours__swiper-button--prev.tours__swiper-button--disabled {
  width: 37px;
  height: 35px;
  background-image: url("../../img/sprite/icon-arrow-disabled.svg");
  pointer-events: none;
}

.tours__swiper-button--next.tours__swiper-button--disabled {
  width: 37px;
  height: 35px;
  rotate: 180deg;
  background-image: url("../../img/sprite/icon-arrow-disabled.svg");
  pointer-events: none;
}
